import React from 'react';
import * as Style from './style';
import kintowariImg from './media/kintowari.jpg';
import kintowariWork1Img from './media/kintowari_work_1.jpg';

export class Kintowari extends React.Component {

    render() {

        let windowWidth = this.props.windowWidth;
        let titleFontSizeRate = 15;
        let titleFontSize = 100/titleFontSizeRate + "vw";

        
        let bioFontSize = 100/Style.workCommonFactor.bioFontSizeRate + "vw";
        let bioPaddingBottom = 100/Style.workCommonFactor.bioPaddingBottomRate + "vw";
        let introFontSize = 100/Style.workCommonFactor.introFontSizeRate + "vw";
        let introPaddingBottom = 100/Style.workCommonFactor.introPaddingBottomRate + "vw";
        let youtubeTop = 100/Style.workCommonFactor.youtubeTopRate + "vw";
        let quoteBorderThick = 100/Style.workCommonFactor.quoteBorderThickRate + "vw";
        let quoteFontSize = 100/Style.workCommonFactor.quoteFontSizeRate + "vw";
        let firstImgTopFromQuote = 100/Style.workCommonFactor.firstImgTopFromQuoteRate + "vw";
        let afterNoQuoteYoutubeImgTop = 100/Style.workCommonFactor.afterNoQuoteYoutubeImgTopRate + "vw";

        // over maxWidth ios
        if(windowWidth === Style.maxWindowWidth) {
            titleFontSize = windowWidth/titleFontSizeRate + "px";
            bioFontSize = windowWidth/Style.workCommonFactor.bioFontSizeRate + "px";
            bioPaddingBottom = windowWidth/Style.workCommonFactor.bioPaddingBottomRate + "px";
            introFontSize = windowWidth/Style.workCommonFactor.introFontSizeRate + "px";
            introPaddingBottom = windowWidth/Style.workCommonFactor.introPaddingBottomRate + "px";
            youtubeTop = windowWidth/Style.workCommonFactor.youtubeTopRate + "px";
            quoteBorderThick = windowWidth/Style.workCommonFactor.quoteBorderThickRate + "px";
            quoteFontSize = windowWidth/Style.workCommonFactor.quoteFontSizeRate + "px";
            firstImgTopFromQuote = windowWidth/Style.workCommonFactor.firstImgTopFromQuoteRate + "px";
            afterNoQuoteYoutubeImgTop = windowWidth/Style.workCommonFactor.afterNoQuoteYoutubeImgTopRate + "px";
        }

        return(
            <div style={Style.workMainDivStyle}>
                <img src={kintowariImg} style={Style.workMainImgStyle}></img>
                <p style={{...Style.workTitleStyle, fontSize:titleFontSize}}>M4L Kinto-wari</p>
                <p style={{...Style.workBiographyStyle, fontSize:bioFontSize, paddingBottom:bioPaddingBottom}}>2018/07, Sound Programer, Max for Live, Ableton Live</p>
                <p style={{...Style.workIntroductionStyle, fontSize:introFontSize, paddingBottom:introPaddingBottom}}>This is a new sense sampler Max for Live device that can be used with Ableton Live. This is a project with <a href="https://twitter.com/KeiKusama" style={Style.workInlineLinkStyle}>Kei Kusama</a>. Starting from <a href="https://camp-fire.jp/en/projects/view/39952" style={Style.workInlineLinkStyle}>this crowdfunding</a>, we gathered 80 supporters. I was in charge of development of Max for Live.</p>
                <p style={{...Style.workIntroductionStyle, fontSize:introFontSize, paddingBottom:introPaddingBottom}}><a href="https://gumroad.com/l/qGIsR" style={Style.workInlineLinkStyle}>Web Store</a></p>
                <div style={{...Style.workYoutubeWrapStyle}}>
                    <iframe style={Style.workYoutubeStyle} width="560" height="315" src="https://www.youtube.com/embed/6V0MZvzneVM" frameBorder="0" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowFullScreen></iframe>
                </div>
                <img src={kintowariWork1Img} style={{...Style.workSubImgStyle, top:afterNoQuoteYoutubeImgTop}}></img>
            </div>
        );
    }
}