import React from 'react';
import * as Style from './style';
import memoryImg from './media/memory.jpg';
import memoryWork1Img from './media/memory_work_1.jpg';
import memoryWork2Img from './media/memory_work_2.jpg';
import memoryWork3Img from './media/memory_work_3.jpg';

export class Memory extends React.Component {

    render() {

        let windowWidth = this.props.windowWidth;
        let titleFontSizeRate = 15;
        let titleFontSize = 100/titleFontSizeRate + "vw";

        let bioFontSize = 100/Style.workCommonFactor.bioFontSizeRate + "vw";
        let bioPaddingBottom = 100/Style.workCommonFactor.bioPaddingBottomRate + "vw";
        let introFontSize = 100/Style.workCommonFactor.introFontSizeRate + "vw";
        let introPaddingBottom = 100/Style.workCommonFactor.introPaddingBottomRate + "vw";
        let youtubeTop = 100/Style.workCommonFactor.youtubeTopRate + "vw";
        let quoteBorderThick = 100/Style.workCommonFactor.quoteBorderThickRate + "vw";
        let quoteFontSize = 100/Style.workCommonFactor.quoteFontSizeRate + "vw";
        let firstImgTopFromQuote = 100/Style.workCommonFactor.firstImgTopFromQuoteRate + "vw";
        let afterYoutubeImgTop = 100/Style.workCommonFactor.afterYoutubeImgTopRate + "vw";
        let afterYoutubeImg2Top = 100/Style.workCommonFactor.afterYoutubeImgTop2Rate + "vw";
        let afterYoutubeImg3Top = 100/Style.workCommonFactor.afterYoutubeImgTop3Rate + "vw";

        // over maxWidth ios
        if(windowWidth === Style.maxWindowWidth) {
            titleFontSize = windowWidth/titleFontSizeRate + "px";
            bioFontSize = windowWidth/Style.workCommonFactor.bioFontSizeRate + "px";
            bioPaddingBottom = windowWidth/Style.workCommonFactor.bioPaddingBottomRate + "px";
            introFontSize = windowWidth/Style.workCommonFactor.introFontSizeRate + "px";
            introPaddingBottom = windowWidth/Style.workCommonFactor.introPaddingBottomRate + "px";
            youtubeTop = windowWidth/Style.workCommonFactor.youtubeTopRate + "px";
            quoteBorderThick = windowWidth/Style.workCommonFactor.quoteBorderThickRate + "px";
            quoteFontSize = windowWidth/Style.workCommonFactor.quoteFontSizeRate + "px";
            firstImgTopFromQuote = windowWidth/Style.workCommonFactor.firstImgTopFromQuoteRate + "px";
            afterYoutubeImgTop = windowWidth/Style.workCommonFactor.afterYoutubeImgTopRate + "px";
            afterYoutubeImg2Top = windowWidth/Style.workCommonFactor.afterYoutubeImgTop2Rate + "px";
            afterYoutubeImg3Top = windowWidth/Style.workCommonFactor.afterYoutubeImgTop3Rate + "px";
        }

        let quoteBorderLeft = `${quoteBorderThick} solid ${Style.myColor.whiteFade}`;

        return(
            <div style={Style.workMainDivStyle}>
                <img src={memoryImg} style={Style.workMainImgStyle}></img>
                <p style={{...Style.workTitleStyle, fontSize:titleFontSize}}>Memory of Topography</p>
                <p style={{...Style.workBiographyStyle, fontSize:bioFontSize, paddingBottom:bioPaddingBottom}}>2018/06, Sound Programer, MaxMsp</p>
                <p style={{...Style.workIntroductionStyle, fontSize:introFontSize, paddingBottom:introPaddingBottom}}>Developed 24.1ch surrund system and interactive system.</p>
                <blockquote style={{...Style.workBlockQuoteStyle, borderLeft:quoteBorderLeft}}>
                    <p style={{...Style.workQuoteStyle, fontSize:quoteFontSize}}>This artwork installation depicts a rural mountain landscape of varying elevations. The scenery instills in the viewer a feeling of eternal permanence, however with the flow of real time the scenery is continuously changing. In the spring, the rice fields are still small and lush. In summer, they begin to mature, and in fall, they turn a golden color. And with the flow of time, the insects and flowers that make an appearance also change. The movement of insects is influenced by the movement and actions of people. And as people move around, the flow of air changes and blows the rice plants or scatters flower petals in different directions.</p>
                    <p style={{...Style.workQuoteStyle, fontSize:quoteFontSize}}>The overall appearance of this work will hardly change with each year. However, as in nature, the scenery is actually changing, and you will never see the same scenery twice. This moment will never be repeated and can never be seen again. Seemingly unchanging landscapes go on year after year for eternity.</p>
                    <p style={{...Style.workQuoteStyle, fontSize:quoteFontSize}}>The work and the canvas that mediates it are separated - the canvas has become transformative. The visual illusion produced by continuous dynamic change causes the viewer to become bodily immersed in the work, and the border between the viewer and artwork dissolves. As the unified world changes, viewers may feel a sense of melting into the world, becoming one with it and with each other.</p>
                    <a href="https://www.teamlab.art/w/topography/" style={{...Style.workQuoteLinkStyle, fontSize:quoteFontSize}}>https://www.teamlab.art/w/topography/</a>
                </blockquote>
                <div style={{...Style.workYoutubeWrapStyle, top:youtubeTop}}>
                    <iframe style={Style.workYoutubeStyle} width="560" height="315" src="https://www.youtube.com/embed/qwOgblaqd-w" frameBorder="0" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowFullScreen></iframe>
                </div>
                <img src={memoryWork1Img} style={{...Style.workMainImgStyle, top:afterYoutubeImgTop}}></img>
                <img src={memoryWork2Img} style={{...Style.workMainImgStyle, top:afterYoutubeImg2Top}}></img>
                <img src={memoryWork3Img} style={{...Style.workMainImgStyle, top:afterYoutubeImg3Top}}></img>
            </div>
        );
    }
}