import React from 'react';
import * as Style from './style';
import amcj_fes2018Img from './media/amcj_fes2018.jpg';

export class Amcj_fes2018 extends React.Component {

    render() {

        let windowWidth = this.props.windowWidth;
        let titleFontSizeRate = 15;
        let titleFontSize = 100/titleFontSizeRate + "vw";

        
        let bioFontSize = 100/Style.workCommonFactor.bioFontSizeRate + "vw";
        let bioPaddingBottom = 100/Style.workCommonFactor.bioPaddingBottomRate + "vw";
        let introFontSize = 100/Style.workCommonFactor.introFontSizeRate + "vw";
        let introPaddingBottom = 100/Style.workCommonFactor.introPaddingBottomRate + "vw";
        let youtubeTop = 100/Style.workCommonFactor.youtubeTopRate + "vw";
        let quoteBorderThick = 100/Style.workCommonFactor.quoteBorderThickRate + "vw";
        let quoteFontSize = 100/Style.workCommonFactor.quoteFontSizeRate + "vw";
        let firstImgTopFromQuote = 100/Style.workCommonFactor.firstImgTopFromQuoteRate + "vw";

        // over maxWidth ios
        if(windowWidth === Style.maxWindowWidth) {
            titleFontSize = windowWidth/titleFontSizeRate + "px";
            bioFontSize = windowWidth/Style.workCommonFactor.bioFontSizeRate + "px";
            bioPaddingBottom = windowWidth/Style.workCommonFactor.bioPaddingBottomRate + "px";
            introFontSize = windowWidth/Style.workCommonFactor.introFontSizeRate + "px";
            introPaddingBottom = windowWidth/Style.workCommonFactor.introPaddingBottomRate + "px";
            youtubeTop = windowWidth/Style.workCommonFactor.youtubeTopRate + "px";
            quoteBorderThick = windowWidth/Style.workCommonFactor.quoteBorderThickRate + "px";
            quoteFontSize = windowWidth/Style.workCommonFactor.quoteFontSizeRate + "px";
            firstImgTopFromQuote = windowWidth/Style.workCommonFactor.firstImgTopFromQuoteRate + "px";
        }

        return(
            <div style={Style.workMainDivStyle}>
                <img src={amcj_fes2018Img} style={Style.workMainImgStyle}></img>
                <p style={{...Style.workTitleStyle, fontSize:titleFontSize}}>Ableton and Max Fes 2018</p>
                <p style={{...Style.workBiographyStyle, fontSize:bioFontSize, paddingBottom:bioPaddingBottom}}>2018/09, Organizer, Presenter, Max for Live, Ableton Live</p>
                <p style={{...Style.workIntroductionStyle, fontSize:introFontSize, paddingBottom:introPaddingBottom}}>AMCJ's first commemorative event at Harajuku VACANT. I made a presentation about the Live API in Max for Live.</p>
                <p style={{...Style.workIntroductionStyle, fontSize:introFontSize, paddingBottom:introPaddingBottom}}><a href="https://ableton-and-max-community-japan.github.io/" style={Style.workInlineLinkStyle}>Official Site</a>, <a href="https://twitter.com/AMCJ_Official" style={Style.workInlineLinkStyle}>Twitter</a></p>
            </div>
        );
    }
}