import React from 'react';
import * as Style from './style';
import cristalUniverseImg from './media/cristalUniverse.jpg';
import cristalUniverseWork1Img from './media/cristalUniverse_work_1.jpg';
import cristalUniverseWork2Img from './media/cristalUniverse_work_2.jpg';
import cristalUniverseWork3Img from './media/cristalUniverse_work_3.jpg';

export class CristalUniverse extends React.Component {

    render() {

        let windowWidth = this.props.windowWidth;
        let titleFontSizeRate = 15;
        let titleFontSize = 100/titleFontSizeRate + "vw";

        let bioFontSize = 100/Style.workCommonFactor.bioFontSizeRate + "vw";
        let bioPaddingBottom = 100/Style.workCommonFactor.bioPaddingBottomRate + "vw";
        let introFontSize = 100/Style.workCommonFactor.introFontSizeRate + "vw";
        let introPaddingBottom = 100/Style.workCommonFactor.introPaddingBottomRate + "vw";
        let youtubeTop = 100/Style.workCommonFactor.youtubeTopRate + "vw";
        let quoteBorderThick = 100/Style.workCommonFactor.quoteBorderThickRate + "vw";
        let quoteFontSize = 100/Style.workCommonFactor.quoteFontSizeRate + "vw";
        let firstImgTopFromQuote = 100/Style.workCommonFactor.firstImgTopFromQuoteRate + "vw";
        let afterYoutubeImgTop = 100/Style.workCommonFactor.afterYoutubeImgTopRate + "vw";
        let afterYoutubeImg2Top = 100/Style.workCommonFactor.afterYoutubeImgTop2Rate + "vw";
        let afterYoutubeImg3Top = 100/Style.workCommonFactor.afterYoutubeImgTop3Rate + "vw";

        // over maxWidth ios
        if(windowWidth === Style.maxWindowWidth) {
            titleFontSize = windowWidth/titleFontSizeRate + "px";
            bioFontSize = windowWidth/Style.workCommonFactor.bioFontSizeRate + "px";
            bioPaddingBottom = windowWidth/Style.workCommonFactor.bioPaddingBottomRate + "px";
            introFontSize = windowWidth/Style.workCommonFactor.introFontSizeRate + "px";
            introPaddingBottom = windowWidth/Style.workCommonFactor.introPaddingBottomRate + "px";
            youtubeTop = windowWidth/Style.workCommonFactor.youtubeTopRate + "px";
            quoteBorderThick = windowWidth/Style.workCommonFactor.quoteBorderThickRate + "px";
            quoteFontSize = windowWidth/Style.workCommonFactor.quoteFontSizeRate + "px";
            firstImgTopFromQuote = windowWidth/Style.workCommonFactor.firstImgTopFromQuoteRate + "px";
            afterYoutubeImgTop = windowWidth/Style.workCommonFactor.afterYoutubeImgTopRate + "px";
            afterYoutubeImg2Top = windowWidth/Style.workCommonFactor.afterYoutubeImgTop2Rate + "px";
            afterYoutubeImg3Top = windowWidth/Style.workCommonFactor.afterYoutubeImgTop3Rate + "px";
        }

        let quoteBorderLeft = `${quoteBorderThick} solid ${Style.myColor.whiteFade}`;

        return(
            <div style={Style.workMainDivStyle}>
                <img src={cristalUniverseImg} style={Style.workMainImgStyle}></img>
                <p style={{...Style.workTitleStyle, fontSize:titleFontSize}}>The Infinite Crystal Universe</p>
                <p style={{...Style.workBiographyStyle, fontSize:bioFontSize, paddingBottom:bioPaddingBottom}}>2018/07, Sound Programer / Sound Designer, MaxMsp</p>
                <p style={{...Style.workIntroductionStyle, fontSize:introFontSize, paddingBottom:introPaddingBottom}}>Developed 24.1ch surrund system and interactive system. And also made the sound of some elements.</p>
                <blockquote style={{...Style.workBlockQuoteStyle, borderLeft:quoteBorderLeft}}>
                    <p style={{...Style.workQuoteStyle, fontSize:quoteFontSize}}>Pointillism uses an accumulation of distinct dots of color to create a picture. Here, light points are used to create three-dimensional objects. This interactive artwork expresses the universe through accumulated light points that spread infinitely in all directions.</p>
                    <p style={{...Style.workQuoteStyle, fontSize:quoteFontSize}}>People can use their smartphones to select elements that make up the universe by dragging them and releasing them into the The Infinite Crystal Universe. Each element of the universe sent into the work influences that of other elements and is influenced by the presence of people in the space.</p>
                    <p style={{...Style.workQuoteStyle, fontSize:quoteFontSize}}>This artwork is forever evolving, changing moment to moment due to the people in the space.</p>
                    <a href="https://www.teamlab.art/w/infinite_crystaluniverse" style={{...Style.workQuoteLinkStyle, fontSize:quoteFontSize}}>https://www.teamlab.art/w/infinite_crystaluniverse</a>
                </blockquote>
                <div style={{...Style.workYoutubeWrapStyle, top:youtubeTop}}>
                    <iframe style={Style.workYoutubeStyle} width="560" height="315" src="https://www.youtube.com/embed/qt3AW9flF4Q" frameBorder="0" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowFullScreen></iframe>
                </div>
                <img src={cristalUniverseWork1Img} style={{...Style.workMainImgStyle, top:afterYoutubeImgTop}}></img>
                <img src={cristalUniverseWork2Img} style={{...Style.workMainImgStyle, top:afterYoutubeImg2Top}}></img>
                <img src={cristalUniverseWork3Img} style={{...Style.workMainImgStyle, top:afterYoutubeImg3Top}}></img>
            </div>
        );
    }
}