import React from 'react';
import * as Style from './style';
import cristalWorldImg from './media/cristalWorld.jpg';
import cristalWorldWork1Img from './media/cristalWorld_work_1.jpg';
import cristalWorldWork2Img from './media/cristalWorld_work_2.jpg';
import cristalWorldWork3Img from './media/cristalWorld_work_3.jpg';

export class CristalWorld extends React.Component {

    
    render() {

        let windowWidth = this.props.windowWidth;
        let titleFontSizeRate = 18;
        let titleFontSize = 100/titleFontSizeRate + "vw";

        let bioFontSize = 100/Style.workCommonFactor.bioFontSizeRate + "vw";
        let bioPaddingBottom = 100/Style.workCommonFactor.bioPaddingBottomRate + "vw";
        let introFontSize = 100/Style.workCommonFactor.introFontSizeRate + "vw";
        let introPaddingBottom = 100/Style.workCommonFactor.introPaddingBottomRate + "vw";
        let youtubeTop = 100/Style.workCommonFactor.youtubeTopRate + "vw";
        let quoteBorderThick = 100/Style.workCommonFactor.quoteBorderThickRate + "vw";
        let quoteFontSize = 100/Style.workCommonFactor.quoteFontSizeRate + "vw";
        let firstImgTopFromQuote = 100/Style.workCommonFactor.firstImgTopFromQuoteRate + "vw";
        let afterYoutubeImgTop = 100/Style.workCommonFactor.afterYoutubeImgTopRate + "vw";
        let afterYoutubeImg2Top = 100/Style.workCommonFactor.afterYoutubeImgTop2Rate + "vw";
        let afterYoutubeImg3Top = 100/Style.workCommonFactor.afterYoutubeImgTop3Rate + "vw";

        // over maxWidth ios
        if(windowWidth === Style.maxWindowWidth) {
            titleFontSize = windowWidth/titleFontSizeRate + "px";
            bioFontSize = windowWidth/Style.workCommonFactor.bioFontSizeRate + "px";
            bioPaddingBottom = windowWidth/Style.workCommonFactor.bioPaddingBottomRate + "px";
            introFontSize = windowWidth/Style.workCommonFactor.introFontSizeRate + "px";
            introPaddingBottom = windowWidth/Style.workCommonFactor.introPaddingBottomRate + "px";
            youtubeTop = windowWidth/Style.workCommonFactor.youtubeTopRate + "px";
            quoteBorderThick = windowWidth/Style.workCommonFactor.quoteBorderThickRate + "px";
            quoteFontSize = windowWidth/Style.workCommonFactor.quoteFontSizeRate + "px";
            firstImgTopFromQuote = windowWidth/Style.workCommonFactor.firstImgTopFromQuoteRate + "px";
            afterYoutubeImgTop = windowWidth/Style.workCommonFactor.afterYoutubeImgTopRate + "px";
            afterYoutubeImg2Top = windowWidth/Style.workCommonFactor.afterYoutubeImgTop2Rate + "px";
            afterYoutubeImg3Top = windowWidth/Style.workCommonFactor.afterYoutubeImgTop3Rate + "px";
        }

        let quoteBorderLeft = `${quoteBorderThick} solid ${Style.myColor.whiteFade}`;

        return(
            <div style={Style.workMainDivStyle}>
                <img src={cristalWorldImg} style={Style.workMainImgStyle}></img>
                <p style={{...Style.workTitleStyle, fontSize:titleFontSize}}>Wander through the Crystal World</p>
                <p style={{...Style.workBiographyStyle, fontSize:bioFontSize, paddingBottom:bioPaddingBottom}}>2018/06, Sound Programer, MaxMsp</p>
                <p style={{...Style.workIntroductionStyle, fontSize:introFontSize, paddingBottom:introPaddingBottom}}>Developed 24.1ch surrund system and interactive system.</p>
                <blockquote style={{...Style.workBlockQuoteStyle, borderLeft:quoteBorderLeft}}>
                    <p style={{...Style.workQuoteStyle, fontSize:quoteFontSize}}>This interactive installation artwork uses an accumulation of light points to create a sculptural body, similar to the way distinct dots of color form an image in a pointillist painting. The accumulation of lights in three-dimensional space form an expression of a world inside the Crystal World.</p>
                    <p style={{...Style.workQuoteStyle, fontSize:quoteFontSize}}>Back when kanji (hieroglyphic characters) were carved into the shell of a turtle, the bone of a cow or deer, or cast in a piece of bronze ware, each character contained an entire world within it. Here, a Crystal World is created when a person summons the worlds contained in the characters up, through their smartphone. Each summoned world has an affect on the world of other visitors', and is influenced by the people in the installation. </p>
                    <p style={{...Style.workQuoteStyle, fontSize:quoteFontSize}}>Butterflies drawn and created by visitors at other teamLab Borderless locations, or through other works of art, can make their way into this world, transformed into 3D creations through points of light.</p>
                    <p style={{...Style.workQuoteStyle, fontSize:quoteFontSize}}>Due to visitor interaction this Crystal World is in perpetual change, the space is reborn moment by moment.</p>
                    <a href="https://www.teamlab.art/w/crystalworld" style={{...Style.workQuoteLinkStyle, fontSize:quoteFontSize}}>https://www.teamlab.art/w/crystalworld</a>
                </blockquote>
                <div style={{...Style.workYoutubeWrapStyle, top:youtubeTop}}>
                    <iframe style={Style.workYoutubeStyle} width="560" height="315" src="https://www.youtube.com/embed/htGsCscBRIk" frameBorder="0" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowFullScreen></iframe>
                </div>
                <img src={cristalWorldWork1Img} style={{...Style.workMainImgStyle, top:afterYoutubeImgTop}}></img>
                <img src={cristalWorldWork2Img} style={{...Style.workMainImgStyle, top:afterYoutubeImg2Top}}></img>
                <img src={cristalWorldWork3Img} style={{...Style.workMainImgStyle, top:afterYoutubeImg3Top}}></img>
            </div>
        );
    }
}