import React from 'react';
import * as Style from './style';
import monsterHunterWorldImg from './media/monsterHunterWorld_work_main.jpg';

export class MonsterHunterWorld extends React.Component {

    render() {

        let windowWidth = this.props.windowWidth;
        let titleFontSizeRate = 16;
        let titleFontSize = 100/titleFontSizeRate + "vw";

        let bioFontSize = 100/Style.workCommonFactor.bioFontSizeRate + "vw";
        let bioPaddingBottom = 100/Style.workCommonFactor.bioPaddingBottomRate + "vw";
        let introFontSize = 100/Style.workCommonFactor.introFontSizeRate + "vw";
        let introPaddingBottom = 100/Style.workCommonFactor.introPaddingBottomRate + "vw";
        let youtubeTop = 100/Style.workCommonFactor.youtubeTopRate + "vw";
        let quoteBorderThick = 100/Style.workCommonFactor.quoteBorderThickRate + "vw";
        let quoteFontSize = 100/Style.workCommonFactor.quoteFontSizeRate + "vw";
        let firstImgTopFromQuote = 100/Style.workCommonFactor.firstImgTopFromQuoteRate + "vw";

        // over maxWidth ios
        if(windowWidth === Style.maxWindowWidth) {
            titleFontSize = windowWidth/titleFontSizeRate + "px";
            bioFontSize = windowWidth/Style.workCommonFactor.bioFontSizeRate + "px";
            bioPaddingBottom = windowWidth/Style.workCommonFactor.bioPaddingBottomRate + "px";
            introFontSize = windowWidth/Style.workCommonFactor.introFontSizeRate + "px";
            introPaddingBottom = windowWidth/Style.workCommonFactor.introPaddingBottomRate + "px";
            youtubeTop = windowWidth/Style.workCommonFactor.youtubeTopRate + "px";
            quoteBorderThick = windowWidth/Style.workCommonFactor.quoteBorderThickRate + "px";
            quoteFontSize = windowWidth/Style.workCommonFactor.quoteFontSizeRate + "px";
            firstImgTopFromQuote = windowWidth/Style.workCommonFactor.firstImgTopFromQuoteRate + "px";
        }

        return(
            <div style={Style.workMainDivStyle}>
                <img src={monsterHunterWorldImg} style={Style.workMainImgStyle}></img>
                <p style={{...Style.workTitleStyle, fontSize:titleFontSize}}>MONSTER HUNTER WORLD</p>
                <p style={{...Style.workBiographyStyle, fontSize:bioFontSize, paddingBottom:bioPaddingBottom}}>2017/08, Sound Designer, Pro Tools, WWISE, Ableton Live, MaxMsp</p>
                <p style={{...Style.workIntroductionStyle, fontSize:introFontSize, paddingBottom:introPaddingBottom}}>As a CAPCOM sound designer, created almost all weapon and armor sounds from foley recording, implemented and adjusted them including other player related sound duties.</p>
                <p style={{...Style.workIntroductionStyle, fontSize:introFontSize, paddingBottom:introPaddingBottom}}><a href="http://www.capcom.co.jp/monsterhunter/world/" style={Style.workInlineLinkStyle}>Official Website</a></p>
                <div style={{...Style.workYoutubeWrapStyle}}>
                    <iframe style={Style.workYoutubeStyle} width="560" height="315" src="https://www.youtube.com/embed/v7EkXg-4VME" frameBorder="0" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowFullScreen></iframe>
                </div>
                <div style={{...Style.workYoutubeWrapStyle, top:youtubeTop}}>
                    <iframe style={Style.workYoutubeStyle} width="560" height="315" src="https://www.youtube.com/embed/TJat9r_aEs8" frameBorder="0" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowFullScreen></iframe>
                </div>
            </div>
        );
    }
}